.header {
  font-family: "TTFirsNeue", sans-serif;

  &__logo {
    grid-column: 1 / 3;
  }
}

.header-top {
  padding: 10px 0 8px;
  background-color: var(--color-mine-shaft);

  &__container {
    align-items: center;
  }
}

.nav {
  grid-column: 4 / 11;

  &__list {
    display: flex;
    justify-content: space-between;
  }
}

.socials {
  justify-self: end;
  grid-column: 11 / 13;

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 30px);
    grid-template-rows: repeat(1, 30px);
    gap: 0 20px;
  }

  &__svg {
    width: 100%;
    height: 100%;
  }
}

.header-bottom {
  padding: 12px 0;
  background-color: var(--color-tundora);

  &__container {
    align-items: center;
  }
}

.telephone {
  grid-column: 1 / 3;

  &__link {
    display: grid;
    align-items: center;
    grid-template-columns: 17px 1fr;
    grid-template-rows: repeat(1, 21px);
    gap: 0;
  }

  &__svg {
    width: 100%;
    height: 100%;
  }

  &__number {
    font-weight: 700;
  }
}

.search-form {
  grid-column: 11 / 13;

  &__wrapper {
    display: grid;
    align-items: center;
    grid-template-columns: 21px 1fr;
    grid-template-rows: repeat(1, 21px);
  }

  &__input {
    display: grid;
    border: 0;
    border-bottom: 1px solid var(--color-mercury);
    font-size: 14px;
    color: var(--color-mercury);
    background-color: inherit;

    &::placeholder {
      color: var(--color-mercury);
    }

    &:focus {
      outline: none;
    }
  }

  &__btn {
    position: relative;
    order: -1;
    width: 100%;
    height: 100%;
  }

  &__svg {
    position: absolute;
    left: 0;
    top: 0;
    width: 17px;
    height: 21px;
  }
}
